:root{
--light-Cyan:rgb(129, 214, 248);
}
body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  .board{
    margin: 30px 0;
    display: grid;
    justify-content: center;
  }
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 34px;
    font-weight: bold;
    line-height: 34px;
    height: 74px;
    width: 74px; 
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;   
  }

  .winsquare{
    border-radius: 9px;
    background: var(--light-Cyan);
    animation-iteration-count:infinite;
    transition: transform 1s;
    transform-style: preserve-3d;
    animation: mymove 0.8s infinite;    
  }

  @keyframes mymove {
    from{ transform: rotateY( 180deg ); }
    to {transform: rotateY( 0deg );}   
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  .game-board{
    text-align: center;
  }
  .stat-table tbody tr{
    margin: 5px;
  }
  .stat-table{
    border-spacing:0; 
    width: 100%;
  }

  .stat-table tbody tr td{
   padding: 12px;
   border: 0px ;
  }
  .stat-table tbody tr:nth-child(even){
    background:black;
    color: white;
  } 
  .stat-table tbody tr:nth-child(odd){
    background:grey;
    color: white;
  } 


  /* ################ History ############## */

  .statistic tbody tr:last-child {
    background: var(--light-Cyan);
    color: black;
    font-weight: 600;
   }
   .history {
     text-align: center;
   }


   /* ######################## buttonz ################## */
   .btn-toolset {
     margin: 20px 0;
    display: inline-block;
    text-align: center;
    white-space: nowrap;
    color: #FFFFFF;
    text-decoration: none;
    line-height: 1.2;
    font-weight: normal;
    font-family: sans-serif;
    font-size: 18px;
    background-color:var(--light-Cyan);
    border-radius: 10px;
    padding: 10px 25px;
    cursor: pointer;
   }
   .btn-toolset:hover {
    background-color: #575757;
    border-color: #0056B3;
   }
   .btn-toolset > strong {
    font-size: 24px;
   }